import React, { useState, useEffect } from "react";
import { getShopDetails, getItems } from "./api"
import Drawer from './drawer';
import Cart from './cart';

const Header = ({ onSearchResult }) => {
  const [shopDetails, setshopDetails] = useState([]);
  const [searchText, setSearchText] = useState('');

  useEffect(() => {
    const fetchDetails = async () => {
      const shopDetails = await getShopDetails();
      setshopDetails(shopDetails);
      document.title = shopDetails.title;
    };
    fetchDetails();
  }, []);

const searchQuery = async (ev) => {

  if(ev.key === 'Enter') {
    const result = await getItems(searchText);
    onSearchResult(result);
    setSearchText('');
  }
}
  return (
    <div className="header">
      <div className="logo-icon">
        <img src={shopDetails.logoURL} alt="Shop Logo"></img>
      </div>
      <div className="shop-title">
        <span>{shopDetails.title}</span>
      </div>
      <div className="search">
        <input id="searchInput" type="text" value={searchText} onChange={ev => setSearchText(ev.target.value)} onKeyPress={searchQuery} placeholder="Search..."/>
      </div>
      <div
        className="cart"
        data-bs-toggle="offcanvas"
        data-bs-target="#cartDrawer"
        aria-controls="cartDrawer"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          fill="#887e7e"
          className="bi bi-cart-plus"
          viewBox="0 0 16 16"
        >
          <path d="M9 5.5a.5.5 0 0 0-1 0V7H6.5a.5.5 0 0 0 0 1H8v1.5a.5.5 0 0 0 1 0V8h1.5a.5.5 0 0 0 0-1H9V5.5z" />
          <path d="M.5 1a.5.5 0 0 0 0 1h1.11l.401 1.607 1.498 7.985A.5.5 0 0 0 4 12h1a2 2 0 1 0 0 4 2 2 0 0 0 0-4h7a2 2 0 1 0 0 4 2 2 0 0 0 0-4h1a.5.5 0 0 0 .491-.408l1.5-8A.5.5 0 0 0 14.5 3H2.89l-.405-1.621A.5.5 0 0 0 2 1H.5zm3.915 10L3.102 4h10.796l-1.313 7h-8.17zM6 14a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm7 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
        </svg>
      </div>
      <Drawer position="end" name="cartDrawer">
          <Cart />
      </Drawer>
    </div>
  );
};

export default Header;
